import { easybankcards , EasybankWhyTopText} from "./EasybankInformation";

export default function EasybankWhy(){
    const {title, text} = EasybankWhyTopText;

    return(
        <div className="easybank-why" id="about">
            <div className="easybank-why-top">
                <h2>{title}</h2>
                <p>{text}</p>
            </div>

            <div className="easybank-why-bottom">
               {
                easybankcards.map( object => {
                    const {title, text, img} = object;
            
                    return(
                        
                        <div className="easybank-why-bottom-card" key={img}>
                            <div className={`easybank-why-bottom-card-img-${img}`}></div>
                            <h3>{title}</h3>
                            <p>{text}</p>
                        </div>
                        
                    )
                })
               }
            </div>
        </div>
    )
}