// scrollToId is a function that's take you down to your "link" target   
import { scrollToId } from "../Bookmark/BookmarkNav";
import { useState } from "react";

export default function EasybankNav(){
    const [hamburgerMenu, setHamburgerMenu] = useState(true)

    const toggleMenu = () => {
        setHamburgerMenu( toggle => !toggle )
    }

    return(
        <div className="easybank-nav easybank-nav-hamburger" id="home">
            <div className="easybank-nav-logo"></div>
            <div className={hamburgerMenu ? "easybank-nav-hamburger-open" : "easybank-nav-hamburger-close"} onClick={toggleMenu}></div>
            <div className={!hamburgerMenu ? "easybank-nav-hamburger-links" : "easybank-nav-links"}>
                <p onClick={ () => scrollToId("home")} >Home</p>
                <p onClick={ () => scrollToId("about")} >About</p>
                <p onClick={ () => alert("Contact us: easybank@gmail.com")} >Contact</p>
                <p onClick={ () => scrollToId("blog")} >Blog</p>
                <p onClick={ () => alert("We need you! please contact us easybank@gmail.com")} >Careers</p>
            </div>
            <button className="easybank-nav-btn">Request Invite</button>
        </div>
    )
}