import { ShortlyText } from "./ShortlyText"

export default function ShortlyStatistics(){
    const { statistics:{title, text}, cardtext } = ShortlyText;
    return(
        <div className="shortly-statistics">
             <div className="shortly-statistics-top">
                <h2>{title}</h2>
                <p>{text}</p>
             </div>
             <div className="shortly-statistics-bottom">
                <div className="shortly-statistics-bottom-hr"></div>
                {cardtext.map( e => {
                    return(
                        <div className="shortly-statistics-bottom-card" key={e.id}>
                            <div className="shortly-statistics-card-image">
                                {e.image}
                            </div>
                            <h3>{e.title}</h3>
                            <p>{e.description}</p>
                        </div>
                    )
                })}
             </div>
        </div>
    )
}

