import { useState } from "react"
import { CopyToClipboard } from 'react-copy-to-clipboard';


const closeIcon = <svg  className="shortenRemoveLink" width="15" height="15" viewBox="0 0 15 15" fill="none" xmlns="http://www.w3.org/2000/svg"> <g clipPath="url(#clip0_189_106)"> <path d="M4.52679 0.518555C4.70759 0.199219 5.07924 0 5.48437 0H9.51563C9.92076 0 10.2924 0.199219 10.4732 0.518555L10.7143 0.9375H13.9286C14.5212 0.9375 15 1.35645 15 1.875C15 2.39355 14.5212 2.8125 13.9286 2.8125H1.07143C0.478795 2.8125 0 2.39355 0 1.875C0 1.35645 0.478795 0.9375 1.07143 0.9375H4.28571L4.52679 0.518555ZM1.07143 3.75H13.9286V13.125C13.9286 14.1592 12.9676 15 11.7857 15H3.21429C2.03237 15 1.07143 14.1592 1.07143 13.125V3.75ZM4.28571 5.625C3.99107 5.625 3.75 5.83594 3.75 6.09375V12.6562C3.75 12.9141 3.99107 13.125 4.28571 13.125C4.58036 13.125 4.82143 12.9141 4.82143 12.6562V6.09375C4.82143 5.83594 4.58036 5.625 4.28571 5.625ZM7.5 5.625C7.20536 5.625 6.96429 5.83594 6.96429 6.09375V12.6562C6.96429 12.9141 7.20536 13.125 7.5 13.125C7.79464 13.125 8.03571 12.9141 8.03571 12.6562V6.09375C8.03571 5.83594 7.79464 5.625 7.5 5.625ZM10.7143 5.625C10.4196 5.625 10.1786 5.83594 10.1786 6.09375V12.6562C10.1786 12.9141 10.4196 13.125 10.7143 13.125C11.0089 13.125 11.25 12.9141 11.25 12.6562V6.09375C11.25 5.83594 11.0089 5.625 10.7143 5.625Z" /> </g> <defs> <clipPath id="clip0_189_106"> <rect width="15" height="15" fill="white"/> </clipPath> </defs> </svg>;

export default function ShortlySearch(){
    // storing the inputvalue inside value, and the links thats has been pushed to the array inside of a useState object
    const [input, setInput] = useState({
        value: "",
        savedLinks: []
    })


    // Argument for showing the input warning below the input and highlights the border when the link address is incorrect
    const inputErrorArgument = (input.value.length > 11 && !input.value.includes('www.') ) || (input.value.length > 7 && !input.value.includes('http://'))
    const inputError = { borderColor:   inputErrorArgument ? "#E7676B" : "transparent", borderWidth: "3px", color: inputErrorArgument ? "#E7676B" : "#000"}

 
    // input onchange event. adding the inutvalue to the input state.
    const changeValue = (event) => {
        const {value} = event.target
        setInput(prev => {
            return{
                ...prev,
                value: value
            }
        })
    }
  
    // key 'EnterEvent'. adding input value to the array and set new input value to blank.
    // !inputValue.match(/^ *$/) === so the user cant spam enter to add blank list
    // cant accept the link without "www." or "http://"
    const inputChange = (event) => {
        const inputValue = document.querySelector("#shortlyInput").value;
       
       
        event.key === 'Enter' && !inputValue.match(/^ *$/) && !inputErrorArgument && input.value.includes('www.') && input.value.includes('http://') && setInput(prev => {
            return{
                savedLinks: [...prev.savedLinks, inputValue],
                value: ""
            }
        })
   
    }

    // add links to array when clicking on the btn
    // !inputValue.match(/^ *$/) === so the user cant spam the btn to add blank list
     // cant accept the link without "www." or "http://"
    const addLink = () => {
        const inputValue = document.querySelector("#shortlyInput").value;
        !inputValue.match(/^ *$/) &&  !inputErrorArgument && input.value.includes('www.') && input.value.includes('http://') && setInput(prev => {
            return{
                savedLinks: [...prev.savedLinks, inputValue],
                value: ""
            }
        })
    }

    // remove links when clicking on the btn
    const removeMe = (thisLink) => {
        setInput(prev => {
            return{
                ...prev,
                savedLinks: prev.savedLinks.filter( links => links !== thisLink  )
            }
        })
    }

    // When clicking on Copy Link Btn the style of the btn will change and the link will be copied
    const changeBtnColor = (thisLink) => {
        const thisBtn =  document.querySelector(`#${thisLink}`)
        thisBtn.style.backgroundColor = "#3A3053";
        thisBtn.style.color = "#FFF"; 
        thisBtn.textContent = "Copied!"; 
    }   
  
    
    // saving list off elements from the savedLinks array, return a link without http://www. or without the 'www.'
    const mappedInput = input.savedLinks.map( (e, i) => {
    return(
    <div className="mappedLinks" key={i}> 
        <p>{e}</p> 
        <div onClick={()=>removeMe(e)}>{closeIcon}</div>
        <div className="mappedLinks-right">
            <p >{e.startsWith('http://www.') ? e.slice(11) : e && e.startsWith('http://') ? e.slice(7) : e && e.startsWith('www.') ? e.slice(4) : e || e}</p>

            <CopyToClipboard text={input.savedLinks[i].slice(11)}>
                <button id={`btn${i.toString()}`} onClick={
                    () => {
                    
                        changeBtnColor(`btn${i.toString()}`)
                        alert(e.startsWith('http://www.') ? e.slice(11) : e && e.startsWith('http://') ? e.slice(7) : e && e.startsWith('www.') ? e.slice(4) : e || e) 
                        
                    }
                }>Copy</button>
            </CopyToClipboard>

        </div>
    </div>)})

    

    return(
        <div className="shortly-search">
            <div className="shortly-search-top">
                <input style={inputError} className={!inputErrorArgument ? "shotly-input-good" : "shotly-input-error" } type="text" value={input.value} onKeyDown={inputChange} id="shortlyInput" onChange={changeValue} placeholder="Shorten a link here..." />
                {  inputErrorArgument  && <small>Please add { (input.value.includes('http:www') && "( you forgot // )")  || (!input.value.includes('www.') ?  "( www. )" : "( http:// )") }</small>}
                <button onClick={addLink}>Shorten It!</button>
            </div>

            <div className="shortly-search-bottom">
                    {mappedInput}
            </div>
        </div>
    )
}