import { scrollToId } from "../Bookmark/BookmarkNav"
import { useState } from "react"

export default function RoomDecorateNav(){
    const [toggle, setToggle] = useState(true)
    // variable made for change svg in hamburger menu
    const openHamburger = <svg width="20" height="14" xmlns="http://www.w3.org/2000/svg"><path d="M20 12v2H0v-2h20zm0-6v2H0V6h20zm0-6v2H0V0h20z" fill="#FFF" fillRule="evenodd"/></svg>;
    const closeHamburger = <svg width="16" height="16" xmlns="http://www.w3.org/2000/svg"><path d="M14.364.222l1.414 1.414L9.414 8l6.364 6.364-1.414 1.414L8 9.414l-6.364 6.364-1.414-1.414L6.586 8 .222 1.636 1.636.222 8 6.586 14.364.222z" fill="#000" fillRule="evenodd" opacity=".201"/></svg>;
    
    const toggleHamburger = () => {
        setToggle(prev => !prev)
    }

    return(
        <>
            <div className="roomDecorate-nav">
                <div className="roomDecorate-nav-left"></div>
                <div className="roomDecorate-nav-right">
                    <p onClick={ () => scrollToId("home")}>home</p>
                    <p onClick={ () => scrollToId("shop")}>shop</p>
                    <p onClick={ () => scrollToId("about")}>about</p>
                    <p onClick={ () => scrollToId("contact")}>contact</p>
                </div>
            </div>

            <div className={toggle ? "roomDecorate-nav-hamburger" : "roomDecorate-nav-hamburger-bgFill"}>
                <div className="hamburger-logo" onClick={toggleHamburger}>{toggle ? openHamburger : closeHamburger}</div>
                {
                    toggle ? <div className="roomDecorate-nav-left"></div> :
                    <div className="roomDecorate-nav-right">
                        <p onClick={ () => scrollToId("home")}>home</p>
                        <p onClick={ () => scrollToId("shop")}>shop</p>
                        <p onClick={ () => scrollToId("about")}>about</p>
                        <p onClick={ () => scrollToId("contact")}>contact</p>
                    </div>
                }       
            </div>
        </>
    )
}

