import { useState } from "react";
import { BottomCenterText, RoomDecorateText} from "./RoomDecorateText";

export default function RoomDecorateHome(){
    // useState to handle page and textContent
    const [page, setPage] = useState(0)
    // ClassName for the pages
    const pageClass = ["roomDecorate-home-topLeft", "roomDecorate-home-topLeft-PageTwo", "roomDecorate-home-topLeft-PageThree" ]

    // Change to next page,
    const nextPage = () => { page === pageClass.length - 1 ? setPage(0) : setPage(prev => prev + 1) }
    // Change back to previouse page
    const prevPage = () => { page === 0 ? setPage(2) :setPage(prev => prev - 1) }
    

    return(
        <div className="roomDecorate-home">
            <div className={pageClass[page]}></div> 
         
            <div className="roomDecorate-home-topCenter">
                <button className="roomDecorate-home-topCenter-prev" onClick={nextPage}></button>
                <button className="roomDecorate-home-topCenter-next" onClick={prevPage}></button>
            </div>

            <div className="roomDecorate-home-topRight">
                <div className="roomDecorate-home-topRight-textbox">
                    <h1>{RoomDecorateText[page].title}</h1>
                    <p>{RoomDecorateText[page].text}</p>
                    <button>
                        {RoomDecorateText[page].footerText.toLocaleUpperCase()}
                        {RoomDecorateText.map(e => e.arrowIcon)} 
                    </button>
                </div>
            </div>

            <div className="roomDecorate-home-bottomLeft"></div>

            <div className="roomDecorate-home-bottomCenter">
                <div className="roomDecorate-home-bottomCenter-textBox">
                    <h3>{BottomCenterText.title.toLocaleUpperCase()}</h3>
                    <p>{BottomCenterText.text}</p>
                </div>
            </div>

            <div className="roomDecorate-home-bottomRight"></div>
        </div>
    )
}


